* {
  box-sizing: border-box;
}

.wrapper {
  display: grid;
  width: 1200px;
  margin: 0 auto;

  grid-template-areas: 
    "header header"
    "navigation content";

  grid-template-rows: 80px 1fr;
  grid-template-columns: 2fr 10fr;
}

.header {
  grid-area: header;
  background: #444;
}

.navbar {
  grid-area: navigation;
  background-color: #888;
}

.content {
  grid-area: content;
  background-color: #bbb;
}